.container {
    font-family: Arial, sans-serif;
    margin: 0 auto;
    max-width: 1200px;
    text-align: center;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border-bottom: 1px solid #ddd;
  }
  
  .nav {
    display: flex;
    align-items: center;
  }
  
  .search {
    padding: 0.5rem;
    margin-right: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .icons i {
    margin: 0 0.5rem;
    cursor: pointer;
  }
  
  .profile {
    padding: 2rem 0;
  }
  
  .profileDetails {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .profileImage {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .editButton {
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .actions button {
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .footer {
    background-color: #000;
    color: #fff;
    padding: 1rem 0;
    margin-top: 2rem;
  }
  
  .footer .socialIcons a {
    margin: 0 0.5rem;
    color: #fff;
    text-decoration: none;
  }
  